import React from 'react';

import './Resume.css';

const Resume = () => {
  if( window.innerWidth <= 400 ) {
    return (
      <div className='box-shadow'>
        <img src={require('../../../assets/resume.png')} className='resume-mobile' type='application/pdf' alt='jp mamuric resume' />
      </div>
    );
  } else {
    return (
      <div>
        <object data={require('./JP_ResumeWb.pdf')} className='resume' type='application/pdf' />
      </div>
    );
  }
}

export default Resume;



